\n    }\n\n    ::-webkit-scrollbar-button:vertical:increment { /* Down */\n        background-image: none;\n    }\n\n    ::-webkit-scrollbar-button:vertical:decrement { /* Up */\n        background-image: none;\n    }\n    .disable-node-caret {\n        .node-caret {\n            display: none;\n\t\t}\n\t\twi-tree-node-virtual > div {\n\t\t\tpadding-left: 13px;\n\t\t}\n    }\n    .switch-label{\n        display: flex;\n        align-items: center;\n        justify-content: space-between;\n        color: #666;\n        font-size: 12px;\n        &>label{\n            margin-bottom: 0;\n        }\n    }\n\n    .switch {\n        position: relative;\n          display: inline-block;\n          width: 35px;\n          height: 16px;\n          margin-left: 10px;\n      }\n\n      .switch input {\n        opacity: 0;\n        width: 0;\n        height: 0;\n      }\n\n      .slider {\n        position: absolute;\n        cursor: pointer;\n        top: 0;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        background-color: #ccc;\n        -webkit-transition: .2s;\n        transition: .2s;\n      }\n\n      .slider:before {\n        position: absolute;\n        content: \"\";\n        height: 10px;\n        width: 10px;\n        left: 3px;\n        bottom: 3px;\n        background-color: white;\n        -webkit-transition: .2s;\n        transition: .2s;\n      }\n\n      input:checked + .slider {\n        background-color: #2196F3;\n      }\n\n      input:focus + .slider {\n        box-shadow: 0 0 1px #2196F3;\n      }\n\n      input:checked + .slider:before {\n        -webkit-transform: translateX(18px);\n        -ms-transform: translateX(18px);\n        transform: translateX(18px);\n      }\n\n      /* Rounded sliders */\n      .slider.round {\n        border-radius: 34px;\n      }\n\n      .slider.round:before {\n        border-radius: 50%;\n      }\n\n\n    /* change style special of wi-base-treeview for basemap */\n    wi-tree-view, wi-tree-view-virtual {\n        & div input {\n            width        : 100%;\n            line-height  : 1.4;\n            padding      : 10px 10px 10px 14px;\n            border       : none;\n            border-bottom: 1px solid #f0f0f0;\n            outline      : none;\n            box-sizing   : border-box;\n\n            &::placeholder {\n                color: #cecece;\n            }\n        }\n    }\n    wi-tree-node .node-content.selected, wi-tree-node-virtual .node-content.selected {\n        background: linear-gradient(to left, #e4e4e4, #fff);\n        font-weight: bold;\n    }\n    wi-table-view {\n        & div.cell {\n            display: table-cell;\n            padding: 7px 10px;\n            border-right: 1px solid #d4d4d4;\n            border-bottom: 1px solid #d4d4d4;\n            cursor: default;\n            position: relative;\n            border-top: none;\n            border-left: none;\n        }\n        & div.table {\n            margin-top: 0;\n        }\n    }\n\n    wi-dropdown-list {\n        &:focus {\n            outline: none !important;\n        }\n\n        .ui-select-bootstrap>.ui-select-match>.btn {\n            border    : none;\n            box-shadow: none;\n            outline   : none;\n            display: flex;\n            align-items: center;\n            font-size: 12px;\n        }\n\n        .btn-default:active:focus {\n            background-color: #fff;\n        }\n        .btn-default.active, .btn-default:active, .open>.dropdown-toggle.btn-default {\n            color: #333;\n            background-color: #fff;\n            background-image: none;\n            border-color: #adadad;\n        }\n\n        .btn-default.focus,\n        .btn-default:focus {\n            color           : #333;\n            background-color: #fff;\n            border-color    : #fff;\n        }\n\n        .form-control {\n            border: none;\n        }\n\n        :focus {\n            outline: none;\n        }\n\n        .form-control:focus {\n            border    : none;\n            box-shadow: none;\n        }\n\n        .btn-default:hover {\n            color           : #333;\n            background-color: #fff;\n            border-color    : #fff;\n        }\n    }\n\n    side-bar>div div.content {\n        overflow-x     : hidden;\n        height         : 100%;\n        display        : flex;\n        overflow-y     : auto;\n        /* padding-left: 15px; */\n        flex-direction : row;\n        wi-tree-node .node-content.search-mode, wi-tree-node-virtual .node-content.search-mode {\n            color: #000;\n        }\n\n    }\n\n    wi-droppable {\n        height: 100%;\n    }\n\n    wi-tree-node, wi-tree-node-virtual {\n\n        .node-caret,\n        .node-label,\n        .node-icon {\n            display      : flex;\n            padding-right: 10px;\n            font-size    : 12px;\n        }\n\n        .node-caret {\n            color    : #cccbcb;\n            font-size: 14px;\n            cursor   : pointer;\n            width    : 16px;\n        }\n\n        .node-content {\n            &.search-mode>div {\n                opacity: 1;\n            }\n            &,\n            &::after {\n                line-height: 3em;\n            }\n\n        }\n    }\n\n    .ui-select-match-text {\n        text-align: right;\n    }\n\n    button.disable {\n        color       : #aaa;\n        border-color: #aaa;\n    }\n    .fn-button {\n        border: 1px solid #d9d9d9;\n        font-size: 10px;\n        border-radius: 2px;\n        background: #fff;\n        padding: 6px 12px 5px 12px;\n        margin-right: 15px;\n\t\tline-height: 1;\n\t\tfont-weight: bold;\n        cursor: pointer;\n        transition   : all 0.2s ease-in;\n\n        &:hover {\n            transition   : all 0.2s ease-in;\n            border: 1px solid #1962a7;\n            color: #1962a7;\n            background: #fff;\n        }\n    }\n\n    .main {\n        display       : flex;\n        height        : 100%;\n        flex-direction: column;\n\n        .main-toolbar {\n            flex-basis : 44px;\n            background : #0074c5;\n            display    : flex;\n            align-items: center;\n            position   : relative;\n\n            .menu-item {\n                margin     : 0 15px;\n                color      : #fff;\n                display    : flex;\n                align-items: center;\n                cursor     : pointer;\n\n                .select-proj4 {\n                    box-shadow   : none;\n                    border       : none;\n                    border-radius: 0;\n                    font-size    : 12px;\n                    color        : #fff;\n                    background   : none;\n                }\n\n                .select-proj4:focus {\n                    border-color      : none;\n                    outline           : none;\n                    -webkit-box-shadow: none;\n                    box-shadow        : none;\n\n                }\n            }\n        }\n        .statistic-table {\n            margin: 15px 15px;\n            position: relative;\n            height: 25%;\n            max-height: 64%;\n            // overflow: auto;\n            // flex-basis: 200px;\n        }\n\n        .main-body {\n            flex      : 1;\n            display   : flex;\n            overflow-x: auto;\n            overflow-y: hidden;\n            position  : relative;\n            flex-direction: column;\n\n            .border-style {\n                border                 : 1px solid #eee;\n                border-top-right-radius: 3px;\n                border-top-left-radius : 3px;\n            }\n\n            .main-body-left {\n                display    : flex;\n                z-index    : 1;\n                height     : calc(100%);\n                // position: absolute;\n\n                .input-search-list-view {\n                    background   : #fff;\n                    width        : 100%;\n                    height       : 44px;\n                    border       : 1px solid #ffffff4a;\n                    border-bottom: 1px solid #eaeaea;\n                    padding      : 10px 20px 10px 15px;\n                    font-weight  : bold;\n                }\n\n                ::-webkit-input-placeholder {\n                    opacity       : 0.5;\n                    color         : rgba(0, 0, 0, 0.253);\n                    /* font-weight: 100; */\n                }\n\n                :focus::-webkit-input-placeholder {\n                    opacity       : 0.5;\n                    color         : rgba(0, 0, 0, 0.253);\n                    /* font-weight: 100; */\n                }\n\n                .input-search-list-view:focus {\n                    outline: none;\n                }\n            }\n\n            .main-body-center {\n                // margin        : 0px 10px 0 15px;\n                flex          : 1;\n                display       : flex;\n                height        : 100%;\n                position      : relative;\n                flex-direction: column;\n                // min-width     : 700px;\n            }\n        }\n\n        .button-update {\n            background   : #0077be;\n            color        : #fff;\n            font-weight  : bold;\n            flex         : 1;\n            padding      : 10px;\n            text-align   : center;\n            margin       : 9px 15px 15px 15px;\n            border-radius: 2px;\n            cursor: pointer;\n            &:hover{\n                background: #1ba0ec;\n                transition: all 0.5s;\n            }\n        }\n\t\t.disabled-click {\n\t\t\tpointer-events: none;\n    \t\topacity: 0.2;\n\t\t}\n\n        .label-list-view {\n            // padding        : 16px 15px;\n            font-weight    : bold;\n            background     : #fff;\n            // border      : 1px solid #eee;\n            display        : flex;\n            // align-items    : center;\n            justify-content: s